.typography {
	color: #444;
	font-family: "proxima-nova",sans-serif;
	font-size: 17px;
	line-height: 1.6;
	padding: 2px 6px;
	-webkit-font-smoothing: antialiased;
}
.typography p,
.typography blockquote,
.typography ul,
.typography ol,
.typography dl,
.typography table,
.typography fieldset {
	 margin: 1em 0;
}
.typography small {font-size: 80%;}

.typography h1,
.typography h2,
.typography h3,
.typography h4,
.typography h5,
.typography h6 {
	color: #333;
	font-family: "proxima-nova-condensed",sans-serif;
	margin: 1em 0 0.5em;
	line-height: 1.3;
	font-weight: normal;
	text-rendering: optimizelegibility;
}
.typography h1 {
	font-size: 45px;
	font-weight: 900;
	line-height: 1.3;
	margin: .75em 0 .5em;
	text-transform: none
}
.typography h2 {
	font-size: 34px;
	font-weight: 900;
}
.typography h3 {
	font-size: 28px;
	font-weight: 900;
}
.typography h4 {
	font-size: 22px;
}
.typography h5 {
	font-size: 18px;
}
.typography h6 {
	font-size: 18px;
}

.typography ul,
.typography ol {
    padding: 0;
    margin-left: 24px;
}
.typography ul ul,
.typography ul ol,
.typography ol ol,
.typography ol ul {
    margin-top: 0;
    margin-bottom: 0;
}
.typography hr {
    clear: both;
    margin: 1.5em 0;
    border: 0;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #fff;
}
.typography .lineheight-small {line-height: 1.1;}
.typography .lineheight-regular {line-height: 1.6;}
.typography .left {float: left;}
.typography .right{float: right;}
.typography img.left  {margin-right: 1em; margin-bottom: 1em;}
.typography img.right {margin-left: 1em; margin-bottom: 1em;}

/* Blockquotes */
.typography blockquote {
	margin: 1.6667em 0;
	padding-left: 1em;
	border-left: 5px solid #008d9d;
}
.typography blockquote p {
	font-size: 20px;
	font-style: italic;
}
.typography blockquote.pull-right {
	float: right;
	margin-left: 1em;
	padding-right: 1em;
	padding-left: 0;
	border-right: 5px solid #008d9d;
	border-left: 0;
}
.typography blockquote.pull-right p,
.typography blockquote.pull-right small {
	text-align: right;
}

/* ---------------------------------------------------------------
Tables
--------------------------------------------------------------- */
.typography table {
	max-width: 100%;
	background-color: transparent;
}
.typography th {
	text-align: left;
}
.typography table {
	width: 100%;
	margin-bottom: 20px;
}
.typography table thead > tr > th,
.typography table tbody > tr > th,
.typography table tfoot > tr > th,
.typography table thead > tr > td,
.typography table tbody > tr > td,
.typography table tfoot > tr > td {
	padding: 8px;
	font-size: 15px;
	line-height: 1.428571429;
	vertical-align: top;
	border-top: 1px solid #dddddd;
}
.typography table thead > tr > th {
	vertical-align: bottom;
	border-bottom: 2px solid #dddddd;
}
table caption + thead tr:first-child th,
table colgroup + thead tr:first-child th,
table thead:first-child tr:first-child th,
table caption + thead tr:first-child td,
table colgroup + thead tr:first-child td,
table thead:first-child tr:first-child td {
	border-top: 0;
}
table tbody + tbody {
	border-top: 2px solid #dddddd;
}
table table {
	background-color: #ffffff;
}
.table-condensed thead > tr > th,
.table-condensed tbody > tr > th,
.table-condensed tfoot > tr > th,
.table-condensed thead > tr > td,
.table-condensed tbody > tr > td,
.table-condensed tfoot > tr > td {
	padding: 5px;
}
.table-bordered {
	border: 1px solid #dddddd;
}
.table-bordered > thead > tr > th,
.table-bordered > tbody > tr > th,
.table-bordered > tfoot > tr > th,
.table-bordered > thead > tr > td,
.table-bordered > tbody > tr > td,
.table-bordered > tfoot > tr > td {
	border: 1px solid #dddddd;
}
.table-bordered > thead > tr > th,
.table-bordered > thead > tr > td {
	border-bottom-width: 2px;
}
.table-striped > tbody > tr:nth-child(odd) > td,
.table-striped > tbody > tr:nth-child(odd) > th {
	background-color: #f9f9f9;
}
.table-hover > tbody > tr:hover > td,
.table-hover > tbody > tr:hover > th {
	background-color: #f5f5f5;
}
